.Cabecalho{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 20px;
    align-items: center;
    background-color: rgb(255, 255, 255);
    margin: 0;
    padding: 0;
    height: 105px;
    width: 100%;
}

.imgCabecalho{
    width: 174px;
    height: 91px;
    border-radius: 15px;
    background-image: url(/public/Imagens/logo.png);
    margin-top: 30px;
}

.header{
    margin-top: 40px;
}

.img{
    height: 85px;
    margin-top: 10px;
}